import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Card, CardContent } from '@/components/ui/card';
import { Carousel, CarouselContent, CarouselItem, CarouselNext, CarouselPrevious } from '@/components/ui/carousel';
import { PlaceHolderImages } from '@/lib/placeholder-images';
import { Section } from './section';
import { Star } from 'lucide-react';

const testimonials = [
  {
    id: 'testimonial-1',
    name: 'Sarah Johnson',
    role: 'CEO, TechInnovate',
    quote: "Alexia's work exceeded our expectations. The new website is not only beautiful but also incredibly fast and user-friendly. A true professional!",
    rating: 5,
  },
  {
    id: 'testimonial-2',
    name: 'Michael Chen',
    role: 'Project Manager, Creative Solutions',
    quote: "Working with Alexia was a breeze. She's a great communicator, meets all deadlines, and delivers high-quality code. Highly recommended.",
    rating: 5,
  },
  {
    id: 'testimonial-3',
    name: 'David Lee',
    role: 'Senior Developer, CodeCrafters',
    quote: "As a collaborator, Alexia is a fantastic team player. Her frontend skills are top-notch, and she's always eager to tackle new challenges.",
    rating: 5,
  },
];

const renderStars = (rating: number) => {
  return Array.from({ length: 5 }, (_, i) => (
    <Star
      key={i}
      className={`h-5 w-5 ${i < rating ? 'text-yellow-400 fill-yellow-400' : 'text-gray-300'}`}
    />
  ));
};

export function TestimonialsSection() {
  const testimonialImages = PlaceHolderImages.filter(img => img.id.startsWith('testimonial-'));

  return (
    <Section
      id="testimonials"
      title="What Others Say"
      description="I'm grateful to have worked with so many talented people and amazing clients."
    >
      <Carousel
        opts={{
          align: "start",
          loop: true,
        }}
        className="w-full max-w-4xl mx-auto"
      >
        <CarouselContent>
          {testimonials.map((testimonial, index) => {
            const image = testimonialImages.find(img => img.id === testimonial.id) || testimonialImages[0];
            return (
              <CarouselItem key={index} className="md:basis-1/2">
                <div className="p-1 h-full">
                  <Card className="h-full flex flex-col justify-between">
                    <CardContent className="pt-6 flex flex-col items-center text-center flex-grow">
                      <div className="flex mb-4">{renderStars(testimonial.rating)}</div>
                      <p className="italic text-muted-foreground mb-6 flex-grow">"{testimonial.quote}"</p>
                      <div className="flex items-center mt-auto">
                        <Avatar className="h-12 w-12 mr-4">
                          <AvatarImage src={image.imageUrl} alt={image.description} data-ai-hint={image.imageHint} />
                          <AvatarFallback>{testimonial.name.charAt(0)}</AvatarFallback>
                        </Avatar>
                        <div>
                          <p className="font-semibold font-headline">{testimonial.name}</p>
                          <p className="text-sm text-muted-foreground">{testimonial.role}</p>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                </div>
              </CarouselItem>
            );
          })}
        </CarouselContent>
        <CarouselPrevious />
        <CarouselNext />
      </Carousel>
    </Section>
  );
}
