import Image from 'next/image';
import Link from 'next/link';
import { Card, CardContent, CardFooter, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Section } from './section';
import { Badge } from '@/components/ui/badge';
import { PlaceHolderImages } from '@/lib/placeholder-images';
import { Github, ExternalLink } from 'lucide-react';

const projects = [
  {
    id: 'project-1',
    title: 'E-commerce Platform',
    description: 'A full-featured e-commerce site with a modern UI, product filtering, and a secure checkout process built with Next.js and Stripe.',
    tags: ['Next.js', 'React', 'Stripe', 'Tailwind CSS'],
  },
  {
    id: 'project-2',
    title: 'Data Dashboard',
    description: 'An interactive dashboard for visualizing complex datasets, featuring real-time updates and customizable charts.',
    tags: ['React', 'D3.js', 'TypeScript', 'Node.js'],
  },
  {
    id: 'project-3',
    title: 'Social Media App',
    description: 'A mobile-first social media application focusing on photo sharing and community engagement, with a clean and intuitive interface.',
    tags: ['React Native', 'Firebase', 'GraphQL', 'Figma'],
  },
];

export function ProjectsSection() {
  const projectImages = PlaceHolderImages.filter(img => img.id.startsWith('project-'));

  return (
    <Section
      id="projects"
      title="Featured Projects"
      description="Here are some of the projects I'm proud of. Each one was a unique challenge and a great learning experience."
      className="bg-secondary"
    >
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
        {projects.map((project, index) => {
          const image = projectImages.find(img => img.id === project.id) || projectImages[0];
          return (
            <Card key={project.id} className="flex flex-col overflow-hidden transition-all duration-300 hover:shadow-xl hover:-translate-y-2 bg-card">
              <CardHeader className="p-0">
                <Image
                  src={image.imageUrl}
                  alt={image.description}
                  data-ai-hint={image.imageHint}
                  width={600}
                  height={400}
                  className="w-full h-48 object-cover"
                />
              </CardHeader>
              <CardContent className="flex-grow pt-6">
                <CardTitle className="font-headline text-xl mb-2">{project.title}</CardTitle>
                <div className="flex flex-wrap gap-2 mb-4">
                  {project.tags.map(tag => (
                    <Badge key={tag} variant="secondary">{tag}</Badge>
                  ))}
                </div>
                <CardDescription>{project.description}</CardDescription>
              </CardContent>
              <CardFooter className="gap-2">
                <Button asChild className="w-full" variant="outline">
                  <Link href="https://github.com" target="_blank">
                    <Github className="mr-2" /> GitHub
                  </Link>
                </Button>
                <Button asChild className="w-full bg-accent text-accent-foreground hover:bg-accent/90">
                  <Link href="#" target="_blank">
                    <ExternalLink className="mr-2" /> Live Demo
                  </Link>
                </Button>
              </CardFooter>
            </Card>
          );
        })}
      </div>
    </Section>
  );
}
